export default {
	data() {
		return {
			pushdata:"pushdata",
			changedata:"changedata",
			empty:{},
			datalist: [], // 词组列表
			workinfo:{},
		}
	},
	methods: {
		getdatalist() {
			console.log(this.workinfo,"当前作品数据")
			let that = this;
			that.$request.get("/place/?projectId="+that.workinfo.id).then(res => {
					console.log(res, "请求的数据")
					that.datalist = res.data;
				})
				.catch(error => {
					console.error('error:', error);
				});
		},
		deletedata(data) {
			let that = this;
			uni.showModal({
			    title: '提示',
			    content: '确定删除此条数据吗',
			    success: function (res) {
			        if (res.confirm) {
						that.$request.delete("/place/" + data.id+"/").then(res => {
								that.getdatalist();
							})
							.catch(error => {
								console.error('error:', error);
							});						
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
		},
		goto_editor(data,type){
			if( type === "pushdata"){
				uni.navigateTo({
					url:"./editor"
				})
			}
			else if(type === "changedata"){
				console.log(data,"data")
				let mapinfo=JSON.stringify(data);
				console.log(mapinfo,"参数")
				uni.navigateTo({
					url:"./editor?mapinfo="+mapinfo
				})
			}
		}
		//跳转到编辑增加页面
	},
	onShow() {
		let workinfo=uni.getStorageSync("workinfo");
		this.workinfo=workinfo;
		this.getdatalist();
	},
	mounted: function() {
		
	

	},
}
